.supplier-requested-documents{

    .list-group {
        margin-bottom: 20px;
        padding-left: 0;
    }

    .list-group-item {
        position: relative;
        display: block;
        margin-bottom: 2px;
        padding: 10px;
        background: #eeeeee;
        font-size: 12px;

        &:first-child {
            border-top-radius: 4px;
        }

        &:last-child {
            margin-bottom: 10px;
            border-bottom-radius: 4px;
        }
    }
}

.fa-download {
    cursor: pointer;
}



